$size: 200px;

.card {
    width: $size;
    height: $size;
    margin: 10px auto;
    background-color: #eee;
    border-radius: 20px;
    cursor: pointer;
    // background-image: url(../../../../../assets/images/administratif.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    position: relative;
    &::after {
        content: '';
        width: $size;
        height: $size;
        position: absolute;
        left: 0;
        border-radius: 20px;
        z-index: 0;
        background-color: rgba(0, 0, 0, 0.3);
    }
    &-content {
        position: absolute;
        width: $size;
        height: $size;
        border-radius: 20px;
        box-shadow: 0px 0px 10px 2px #000000b0, inset 0 0 10px 2px #ffffffb0;
        display: table;
        z-index: 1;
        span {
            display: table-cell;
            vertical-align: middle;
            padding: 10px;
            font-weight: 600;
        }
    }
}

@media (max-width: 610px) {
    $size: 150px;
    .card {
        width: $size;
        height: $size;
        &::after {
            width: $size;
            height: $size;
        }
        &-content {
            width: $size;
            height: $size;
        }
    }
}
