.menu {
    height: 80px;
    width: 100%;
    position: absolute;
    top: 20px;
    transition: .2s;
    z-index: 10;
    &-fix {
        position: fixed;
        top: 0;
        background-color: rgba(0, 0, 0, .3);
        box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, .3);
    }
    .items {
        height: 80px;
        &.right {
            position: absolute;
            right: 40px;
            top: 5px;
        }
        &.left {
            display: table;
            height: 80px;
            width: 20%;
            margin-left: 40px;
            & li {
                display: table-cell;
                vertical-align: middle;
                text-align: center;
                & a {
                    display: block;
                }
            }
        }
    }
    ul {
        list-style: none;
        margin-block-start: 0;
        margin-block-end: 0;
        padding-inline-start: 0;
    }
}