@import '../../../../assets/style/theme.scss';

.services {
    margin-top: -5vh;
    margin-bottom: 120px;
    .title {
        color: $primary;
        text-align: center;
    }
    &-list {
        width: 70%;
        margin: 0 15%;
        display: inline-flex;
        flex-wrap: wrap;
        position: absolute;
        z-index: 1;
    }
}

@media (max-width: 1260px) {
    .services {
        margin-bottom: 340px;
        &-list {
            width: 46%;
            margin: 0 27%;
        }
    }
}

@media (max-width: 980px) {
    .services {
        &-list {
            width: 60%;
            margin: 0 20%;
        }
    }
}

@media (max-width: 760px) {
    .services {
        &-list {
            width: 70%;
            margin: 0 15%;
        }
    }
}

@media (max-width: 480px) {
    .services {
        &-list {
            width: 90%;
            margin: 0 5%;
        }
    }
}

@media (max-width: 333px) {
    .services {
        margin-bottom: 550px;
    }
}